import React, { useState, useEffect, useRef } from "react";
import { BiSearch } from "react-icons/bi";
export default function App() {
  const [imag, setImag] = useState();
  const [more, setMore] = useState(false);
  const [load, setLoad] = useState(false);
  const [inp, setInp] = useState("");
  document.body.style.backgroundColor = "  #FAE2F1";
  const reff = useRef();
  const f = () => {
    fetch(
      `https://api.unsplash.com/search/photos?page=1&query=${reff.current.value}&client_id=UyQgN50-wkwhd_M-sQaSbtLQa_lqmkZ0mcWPjqwOH2w`
    )
      .then((response) => response.json())
      .then((data) => {
        console.log(data);
        setImag(data.results);
        console.log();
      });
  };

  useEffect(() => {
    f();
  }, []);

  return (
    <>
      <div className="container  my-5">
        <form
          onSubmit={(e) => {
            e.preventDefault();
          }}
        >
          <div className="d-flex justify-content-center">
            <div
              style={{
                height: "40px",
                width: "500px",

                backgroundColor: "white"
              }}
            >
              <input
                style={{
                  paddingLeft: "12px",
                  fontSize: "19px",
                  fontFamily: "'Roboto Condensed', sans-serif",
                  outline: "none",
                  boxShadow: " 0 1px 4pt 1pt #999999",
                  height: "100%",
                  border: "none",
                  width: "100%"
                }}
                type="text"
                ref={reff}
                placeholder="Search for photos"
              />
            </div>
            <div style={{ marginLeft: "19px" }}>
              <button
                style={{
                  borderRadius: "2px",
                  backgroundColor: "black",
                  padding: "6px 21px"
                }}
                onClick={() => {
                  f();
                  setInp(reff.current.value);
                }}
              >
                <BiSearch size={20} color="white" />
              </button>
            </div>
          </div>
        </form>
      </div>

      <h3
        style={{
          fontFamily: "'Roboto Condensed', sans-serif",
          fontSize: "23px",
          fontWeight: "700",
          marginLeft: "43px",
          color: "black"
        }}
      >
        {inp}
      </h3>
      <div className="mx-auto text-center">
        {more
          ? imag &&
            imag
              .slice(0, 10)
              .map((s, i) => (
                <img
                  style={{ width: "250px", objectFit: "contain" }}
                  className="py-2 px-3"
                  src={s.urls.thumb}
                  key={i}
                />
              ))
          : imag &&
            imag
              .slice(0, 8)
              .map((s, i) => (
                <img
                  style={{ width: "250px", objectFit: "contain" }}
                  className="py-2 px-3"
                  src={s.urls.thumb}
                  key={i}
                />
              ))}
      </div>

      <br />

      {inp ? (
        <div className="text-center mb-4">
          <button
            onClick={() => {
              setMore((k) => !k);
            }}
            style={{
              fontFamily: "'Roboto Condensed', sans-serif",
              fontWeight: "400",
              color: "white",
              borderRadius: "2px",
              backgroundColor: "black",
              padding: "6px 21px"
            }}
          >
            {more ? "load less" : "load more"}
          </button>
        </div>
      ) : (
        ""
      )}
    </>
  );
}
